import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { Carousel } from 'react-responsive-carousel';
import Slide from '@material-ui/core/Slide';

import { toast } from 'react-toastify';
import './styles.css';
import agent from '../../../agent';
import Loader from '../../../common/Components/Loader';
import DisabledDiv from '../../../common/Components/DisabledDiv';
import TextInput from '../../../common/Components/TextInput';
import SelectImage from '../../../common/Components/SelectImage';
import {
  GET_PRODUCTS,
} from '../../../constants/actionTypes';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      isCreateProductDialog: false,
      user: {},
      product: { },
      images: [],
    };
    this.togelIsCreateProductDialog = this.togelIsCreateProductDialog.bind(this);
    this.deleteImage = this.deleteImage.bind(this);
  }

  componentWillMount() {
    if (this.props.user && this.props.user.user) {
      this.setState({ user: this.props.user.user });
    }
  }

  componentWillReceiveProps(np) {
    if (np.user) {
      if (this.state.user !== np.user.user) {
        const temp = this.props.user.user.user_type === 'normal';
        this.setState({ user: np.user.user, loading: false, isNormalUser: temp });
      }
    }
  }

  togelIsCreateProductDialog() {
    this.setState({ isCreateProductDialog: !this.state.isCreateProductDialog });
  }

  saveChanges(e) {
    if (e) {
      e.preventDefault();
    }
    console.log(this.state.product);
  }

  onSelectFile(file) {
    const temp = [...this.state.images, file];
    this.setState({ images: temp });
    console.log(temp);
  }

  deleteImage(index) {
    const temp = this.state.images.filter((img, i) => i !== index);
    this.setState({ images: temp });
  }

  render() {
    const {
      product, isCreateProductDialog, isNormalUser, images,
    } = this.state;
    return (
      <div>
        <div className=" center  flex flex-column ">
          <button
            className="   dyna-width tc center  mt2 color-btn"
            style={{ padding: '15px', borderRadius: '15px' }}
            onClick={this.togelIsCreateProductDialog.bind(this)}
          >
              Add new Product
          </button>
        </div>
        <Dialog
          open={isCreateProductDialog}
          TransitionComponent={Transition}
          keepMounted
          maxWidth
          onClose={this.togelIsCreateProductDialog}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"

        >

          <DialogTitle id="alert-dialog-slide-title">Create Product</DialogTitle>
          <form onSubmit={this.saveChanges.bind(this)} className="create-product-form">
            <DialogContent style={{
              flexWrap: 'wrap', position: 'relative', display: 'flex', flexDirection: 'column', alignItems: 'center',
            }}
            >
              <div className="product-admin-img-scroll">
                {
                    images.map((img, i) => (
                      <div className="product-admin-img-div">
                        <img src={img.imageDataUrl} alt="" />
                        <button className="" onClick={() => this.deleteImage(i)} type="button">
                            
                          <DeleteForeverIcon />
                        </button>

                      </div>
                    ))
                }

              </div>
              <button className="color-btn center  relative ">
                Add Image
                <SelectImage onSelectFile={this.onSelectFile.bind(this)} />
              </button>
              <TextInput label="Name" value={product.name} type="text" className="w-100" onTextChange={(text) => this.setState({ product: { ...product, name: text } })} required />
              <TextInput label="Descreption" value={product.desc} type="text" className="w-100" onTextChange={(text) => this.setState({ product: { ...product, desc: text } })} required />
              <TextInput label="Discounted Price" value={product.disc_price} className="w-100" type="number" onTextChange={(text) => this.setState({ product: { ...product, disc_price: text } })} required />
              <TextInput label="Price" value={product.price} type="number" className="w-100" onTextChange={(text) => this.setState({ product: { ...product, price: text } })} required />
              <TextInput label="Stock" value={product.stock} type="number" className="w-100" onTextChange={(text) => this.setState({ product: { ...product, stock: text } })} required />
            </DialogContent>
            <DialogActions>
              <button type="submit" color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>
                    Create Product
              </button>
              <button onClick={this.togelIsCreateProductDialog} color="primary" className="btn ba ">
                Cancel
              </button>

            </DialogActions>
          </form>
          {
            this.state.loading ? <Loader /> : null
          }
          {
            isNormalUser
              ? (
                <DisabledDiv message="Purchase a pro pack to get this utility." />
              ) : null
          }
        </Dialog>
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,

});

const mapDispatchToProps = (dispatch) => ({
  setProducts: (products) => dispatch({ type: GET_PRODUCTS, payload: products }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
