import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';

import './ProductsPage.css';



import { toast } from 'react-toastify';


import SideDrawer from '../AdminPage/components/SideDrawer';
import { store } from '../../store';
import agent from '../../agent';
import {
  GET_PRODUCTS,
} from '../../constants/actionTypes';
import Tabs from '../../common/Components/Tabs';
import Loader from '../../common/Components/Loader';
import HomePage from '../HomePage/HomePage';
import IsEmailVerified from '../IsEmailVerified/IsEmailVerified';
import CopyUrl from '../../common/Components/CopyUrl';
import CreateProduct from './components/CreateProduct'

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      user: {},

    };
    this.products = [];
  }

  componentWillMount() {
    // console.log(this.props);
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
    if (this.props.user && this.props.user.user) {
      this.setState({ user: this.props.user.user });
    }
    this.props.getProducts();
  }

  componentWillReceiveProps(np) {
    if (np.user) {
      if (this.state.user !== np.user.user) {
        this.setState({ user: np.user.user, loading: false });
      }
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', () => console.log('removed'));
  }


  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }


  editLinkComponent() {
    return (
      <article className="  ">
        <IsEmailVerified />
        <div className="justify-center  self-center ">
          <CopyUrl /> 
          <CreateProduct/>
        </div>
        
        
        
      </article>

    );
  }

  previewComponent() {
    return (
      <div className="admin-home-page">
        <HomePage parent="admin-page" />
      </div>
    );
  }

  render() {
    const { username } = this.state.user;
    const { loading, mobileView } = this.state;
    return (
      <div>
        <SideDrawer user={this.state.user} />


        {
            mobileView

              ? (
                <div className="nav-margin admin-bg">
                  <Tabs tabsData={[{ tabHeading: 'Edit Links', components: this.editLinkComponent.bind(this) }, { tabHeading: 'Preview', components: this.previewComponent.bind(this) }]} />
                </div>
              )
              : (
                <div className="admin-bg nav-margin flex ">
                  {
                    this.editLinkComponent(username)
                  }
                  {
                    this.previewComponent()
                  }

                </div>
              )
          }

        {
          loading ? <Loader /> : null
        }
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  products: state.ProductsPageReducers.products,
});

const mapDispatchToProps = (dispatch) => ({
  getProducts: () => dispatch({ type: GET_PRODUCTS, payload: agent.ProductsPage.getProducts() }),
  setProducts: (products) => dispatch({ type: GET_PRODUCTS, payload: products }),

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
